import React from 'react';
import useScrollFadeIn from '..//hooks/useScrollFadeIn';
import bride from '../assets/img/bride.png';
import groom from '../assets/img/groom.png';
import married from '../assets/img/married.png';
import open from '../assets/img/open.png';

const Information = () => {
    const animatedItem = useScrollFadeIn('up', 1, 0);
    return (
        <div {...animatedItem} className="content move">
            <h2 className="title"> </h2>
            <div className="info">
                <div className="boxWrap">
                    <div className="box start"></div>
                    <div className="box">
                        <img src={married}></img>
                        <div>
                            <span>(2)</span>
                        </div>
                    </div>
                    <div className="box">
                        <img src={bride}></img>
                        <div>
                            <span>(5)</span>
                        </div>
                    </div>
                    <div className="box">
                        <img className="open" src={open}></img>
                        <div>
                            <span>(4)</span>
                        </div>
                    </div>
                    <div className="box last"></div>
                </div>
                <div className="boxWrap grid">
                    <div className="box start"></div>
                    <div className="box">
                        <div className="top">
                            <div className="time">12:00</div>
                        </div>
                        <div className="bottom"></div>
                    </div>
                    <div className="box">
                        <div className="time">12:30</div>
                        <img src={groom}></img>
                        <div>
                            <span>(3)</span>
                        </div>
                    </div>
                    <div className="box">
                        <div className="time">13:00</div>
                    </div>
                    <div className="box last"></div>
                </div>
                <div className="boxWrap">
                    <div className="box start"></div>
                    <div className="box">
                        <div className="time">14:40</div>
                        <img src={married}></img>
                        <div>
                            2 <span>(1)</span>
                        </div>
                    </div>
                    <div className="box">
                        <div className="time">14:00</div>
                        <img className="open" src={open}></img>
                        <div>
                            <span>(1)</span>
                        </div>
                    </div>
                    <div className="box">
                        <div className="time">13:30</div>
                        <img src={married}></img>
                        <div>
                             <span>(5)</span>
                        </div>
                    </div>
                    <div className="box last"></div>
                </div>
            </div>
        </div>
    );
};

export default Information;
